.content-container {
    position: absolute;
    max-width: 427px;
    width: 100%;
    height: 473.99px;
    left: 256px;
    top: 206px;
    z-index: 2;

    @media (max-width: 415px) {
        top: 92px;
        left: 0;
    }

    @media screen and (min-width: 415px) and (max-width: 913px) {
        top: 170px;
        left: 150px;
    }



    .webinar {
        max-width: 119px;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #4252D1;
        border-radius: 5px;
        gap: 6px;

        @media (max-width: 415px) {
            max-width: 89px;
            width: 100%;
            margin: auto;
        }

        @media screen and (min-width: 415px) and (max-width: 913px) {
            max-width: 110px;
            width: 100%;
            margin: auto;
        }



        img {
            padding-left: 13px;
            max-width: 17.19px;
            width: 100%;

            @media (max-width: 415px) {
                max-width: 12.86px;
                width: 100%;
            }

        }

        p {
            padding: 4px 13px 4px 0;
            line-height: 24px;
            font-weight: 600;
            color: #fff;

            @media (max-width: 415px) {
                font-weight: 600;
                font-size: 12px;
                line-height: 18px;
            }

        }
    }

    h1 {
        max-width: max-content;
        width: 100%;
        height: 95.47px;
        top: 27px;

        font-style: normal;
        font-weight: 800;
        font-size: 77px;
        line-height: 94px;
        text-transform: uppercase;

        color: #FF3459;

        @media (max-width: 415px) {
            font-weight: 800;
            font-size: 52.7273px;
            line-height: 65px;
            margin: auto;
            height: max-content;
        }

        @media screen and (min-width: 415px) and (max-width: 913px) {
            margin: auto;
        }


    }

    .desc-name {
        display: flex;
        align-items: center;

        gap: 16.27px;

        @media (max-width: 415px) {
            justify-content: center;
            top: 330px;
            max-width: 420px;
            width: 100%;
            margin: auto;
        }



        .red-box {
            display: flex;
            align-items: center;
            justify-content: center;
            max-width: 106.72px;
            width: 100%;
            height: 38.69px;


            background: #FF3459;
            border-radius: 5px;
            transform: rotate(-3.51deg);

            @media (max-width: 415px) {
                max-width: 76.26px;
                width: 100%;
                height: 28.69px;
            }


            .text {
                display: flex;
                justify-content: center;
                max-width: 83px;
                width: 100%;
                height: 28.89px;

                font-weight: 800;
                font-size: 23.6391px;
                line-height: 29px;

                text-transform: uppercase;
                transform: rotate(3.51deg);


                color: #FFFFFF;


                @media (max-width: 415px) {
                    margin: auto;
                    margin-top: 4px;
                    left: 5px;
                    max-width: 58.65px;
                    font-weight: 800;
                    font-size: 16px;
                    line-height: 20px;
                }

            }
        }

        h3 {
            font-weight: 600;
            font-size: 22px;
            line-height: 26px;

            color: #FFF;

            @media (max-width: 415px) {
                font-weight: 600;
                font-size: 15px;
                line-height: 18px;
            }

        }
    }


    .desc,
    .desc-mobile {

        margin-top: 20.46px;

        @media (max-width: 415px) {
            margin-top: 16px;
            display: flex;
            justify-content: center;
        }

        @media screen and (min-width: 415px) and (max-width: 913px) {
            display: flex;
            justify-content: center;
        }

        span {

            font-weight: 400;
            font-size: 18px;
            line-height: 26.64px;

            color: #FFFFFF;

            @media (max-width: 415px) {
                padding: 0 16px;
                text-align: center;
                font-weight: 300;
                font-size: 14px;
                line-height: 21px;
            }

            @media screen and (min-width: 415px) and (max-width: 913px) {
                text-align: center;
            }
        }



        h5 {
            position: absolute;
            top: 266px;
            left: 98px;
            max-width: 90px;
            width: 100%;
            padding: 1px 0px 0px 8px;

            border-radius: 5px;
            font-weight: 600;
            font-size: 18px;
            line-height: 27px;
            background-color: #4252D1;

            color: #FFFFFF;

            @media (max-width: 415px) {
                display: none;
            }
        }
    }

    .desc {

        @media (max-width: 913px) {
            display: none;
        }


    }

    .desc-mobile {
        span {
            display: none;

            @media (max-width: 913px) {
                display: block;
            }
        }

        h5 {
            display: none;
            font-weight: 600;
            font-size: 14px;
            line-height: 21px;
            top: 195px;
            background-color: transparent;
            max-width: max-content;
            color: #4252D1;

            @media (max-width: 415px) {
                display: block;
            }

            @media screen and (min-width: 415px) and (max-width: 913px) {
                display: block;
                top: 260px;
                font-weight: 600;
                font-size: 18px;
                line-height: 26.64px;
            }
        }
    }

    .person {
        display: flex;
        margin-top: 52px;
        width: 375px;
        height: 55.59px;
        gap: 16.65px;

        @media (max-width: 415px) {
            display: none;
        }
        @media screen and (min-width: 415px) and (max-width: 913px) {
            position: absolute;
            top: 800px;
            left: 50px;
        }



        span {
            display: flex;
            gap: 4px;

            font-weight: 700;
            font-size: 13px;
            line-height: 16px;
            text-transform: uppercase;
            color: #FFFFFF;



            p {
                color: #4252D1;


            }
        }

        p {
            font-weight: 400;
            font-size: 12px;
            line-height: 19px;

            color: #FFFFFF;

            @media screen and (min-width: 415px) and (max-width: 913px) {
                max-width: 250px;
                width: 100%;
            }

        }
    }

    .bonus {
        display: flex;
        align-items: center;
        gap: 35.5px;
        margin-top: 16px;


        font-weight: 700;
        font-size: 13px;
        line-height: 16px;

        @media (max-width: 415px) {
            display: none;
        }
        
        @media screen and (min-width: 415px) and (max-width: 913px) {
            position: absolute;
            top: 920px;
            left: 50px;
        }



        img {
            width: 37.81px;
            height: 37.81px;


        }

        span {
            display: flex;
            max-width: 130px;
            width: 100%;

            font-weight: 700;
            font-size: 12px;
            line-height: 17, 76px;
            color: #FFFFFF;



        }

        p {
            max-width: 210px;
            width: 100%;
            margin-top: 1.4px;

            font-weight: 400;
            font-size: 12px;
            line-height: 18px;

            color: #FFFFFF;


        }
    }
}

strong {
    font-weight: 600;

    @media (max-width: 415px) {
        font-weight: 500;
    }
}